.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
}

.modalContainer {
  width: 50%;
  max-width: 600px;
  display: flex;
  z-index: 3;
  border-radius: 7px;
  flex-direction: column;
  background-color: white;
  padding: 40px;
  margin: 40px 0;
  overflow-y: scroll;
  scrollbar-width: thin;
}

.titleModal {
  font-size: 24px;
  color: black;
  margin: 0;
  padding-bottom: 10px;
}

.propertyTable {
  align-self: center;
  width: 100%;
  display: flex;
  border-bottom: 1px solid rgb(0, 0, 0, 0.5);
}

.propertyTitle,
.propertyInfo {
  width: 50%;
  margin: 0;
  padding: 10px;
}

.propertyTitle {
  font-size: 16px;
  color: #1c3c5b;
  font-weight: bold;
}

.propertyMoreInfo {
  text-align: justify;
}

@media screen and (max-width: 800px) {
  .propertyTable {
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(0, 0, 0, 0.5);
  }

  .propertyTitle,
  .propertyInfo {
    border: none;
    padding: 10px 0 0;
    width: max-content;
  }
}
